.imp-r {
  margin-top: 160px;
  margin-bottom: 160px;
  display: flex;
  gap: 40px;
  flex-direction: column;
  h1 {
    font-family: "Albert Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    /* identical to box height */

    letter-spacing: 0.02em;

    /* blue nowa */

    color: #0e1a2f;
  }
  h2 {
    font-family: "Albert Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.02em;

    /* blue nowa */

    color: #0e1a2f;
  }
}
