footer {
  padding: 110px 0;
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  @media (max-width: 1024px) {
    gap: 60px;
    grid-template-columns: repeat(2, 1fr);
    padding: 50px 0;
  }
  ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
    &:not(:first-child) {
      text-align: right;
    }
    @media (max-width: 1024px) {
      &:nth-child(3) {
        text-align: left;
      }
    }
    li {
      font-family: "Albert Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.1em;
      color: #0e1a2f;
      width: 100%;
    }
    &:nth-child(1) {
      li {
        &:nth-child(3) {
          margin-top: 110px;
          @media (max-width: 1024px) {
            margin-top: 0px;
          }
        }
      }
    }
  }

  .fl {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    li {
      font-family: "Albert Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.1em;
      color: var(--color-blue);
    }
  }
  .fr {
    display: flex;
    flex-direction: row;
    gap: 150px;
    ul {
      list-style: none;
      display: flex;
      flex-direction: column;
      gap: 24px;
      li {
        cursor: pointer;
        font-family: "Albert Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.1em;
        text-align: right;
        a {
          color: var(--color-blue);
        }
      }
    }
  }
}
